<template>
  <div class="login-wrap">
    <el-form
      class="login-form"
      label-position="top"
      label-width="80px"
      :model="formdata">
      <h2></h2>
      <el-form-item label="">
        <el-input v-model="formdata.username" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formdata.password" placeholder="" show-password></el-input>
      </el-form-item>
      <el-button class="login-btn" type="primary" @click.prevent="handleLogin"></el-button>
    </el-form>
  </div>
</template>

<script>
// qs
import qs from 'qs'

export default {
  data () {
    return {
      formdata: {
        username: '',
        password: ''
        // msg: ''
      }
    }
  },
  methods: {
    // 
    async handleLogin () {
    //    
    // ES7  async+await
      const res = await this.$http.post('login', qs.stringify(this.formdata))
      const {
        data,
        meta: {msg, status}
      } = res.data
      console.log(res)
      if (status == 200) {
      // 
        // 0.token
        localStorage.setItem('token', data.token)
        localStorage.setItem('usersname', data.username)
        // 1.home
        console.log(res)
        this.$router.push({name: 'home'})
        // 2.
        this.$message.success(msg)
      } else {
        this.$message.error(msg)
      }
    }
  }

}
</script>

<style scoped>
  .login-wrap {
    height: 100%;
    background: url(../../assets/3.gif) no-repeat center center fixed;
    /**/
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .login-wrap .login-form {
    opacity: 0.85;
    width: 400px;
    background-color: #ffffff;
    border-radius: 5px;
    padding: 30px;
  }

  .login-wrap .login-btn {
    width: 100%;
  }
</style>



