<template>
  <div>
    <!--  -->
    <el-card>
      <!--  -->
      <my-bread level1="" level2=""></my-bread>
      <el-row :gutter="20" style="margin-top:25px">
        <el-col :span="6">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getorderList">
            <el-button slot="append" icon="el-icon-search" @click="getorderList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="houseList" style="width: 100%;margin-top:25px" border stripe>
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="">
                <span>{{ props.row.orderid }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.houseid }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.housename }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.occupants }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.phonenum}}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.checkintime|fmtdata }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.days }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.price }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.conditional|fmtstate }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column label="" prop="orderid"></el-table-column>
        <el-table-column label="" prop="housename" width="300px"></el-table-column>
        <el-table-column label="" prop="occupants"></el-table-column>
        <el-table-column label="" prop="checkintime">
          <template slot-scope="scope">
          {{scope.row.checkintime|fmtdata}}
        </template>
        </el-table-column>
        <el-table-column label="" prop="days"></el-table-column>
        <el-table-column label="" prop="conditional">
           <template slot-scope="scope">
          {{scope.row.conditional|fmtstate}}
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
      style="margin-top:20px"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 
      houseList: [],
      // 
      total: 0
    }
  },
  created () {
    this.getorderList()
  },
  // 
  filters: {
    fmtstate: function (value) {
      let stateArr = ['', '', '', '', '', '']
      return stateArr[value]
    }
  },
  methods: {
    // 
    async getorderList () {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })
      console.log(res)
      if (res.meta.status != 200) {
        return this.$message.error('')
      }
      this.houseList = res.data.data
      this.total = res.data.total
    },
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getorderList()
    },
    handleCurrentChange (newSize) {
      this.queryInfo.pagenum = newSize
      this.getorderList()
    }
  }
}
</script>

<style scoped>
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
</style>



