<template>
  <el-card class="box-card">
    <!--    1.-->
    <!--    //-->
    <!-- <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>-->
    <my-bread level1="" level2=""></my-bread>
    <!--    2.-->
    <el-row class="searchRow">
      <el-col>
        <el-input
          placeholder=""
          v-model="query"
          class="inputSearch"
          clearable
          @clear="loadUserList"
        >
          <el-button @click="searchUser" slot="append" icon="el-icon-search"></el-button>
        </el-input>
      </el-col>
    </el-row>
    <!--    3.-->
    <el-table :data="userlist" style="width: 100%">
      <!--      type='index'-->
      <el-table-column type="index" label="#" width="100"></el-table-column>
      <el-table-column prop="account" label="ID"></el-table-column>
      <el-table-column prop="nick" label=""></el-table-column>
      <el-table-column prop="cellphone" label=""></el-table-column>
      <el-table-column prop="idnumber" label="" width="200"></el-table-column>

      <el-table-column label="">
        <!--  
        template-->

        <!--
            template slot-scope
            create_timeuserlist
        -->

        <!--
            slot-scopescopeel-tableuserlist
            scope.row->
        -->

        <!--  slot-scope-->
        <template slot-scope="scope">{{scope.row.createtime|fmtdata}}</template>
      </el-table-column>

      <el-table-column label="">
        <template slot-scope="scope">
          <!--          -->
          <el-switch
            @change="changeMgState(scope.row)"
            v-model="scope.row.juisdiction"
            active-color="#13ce66"
            inactive-color="#ff4949"
          ></el-switch>
        </template>
      </el-table-column>
      <el-table-column prop="address" label="">
        <template slot-scope="scope">
          <el-button
            size="mini"
            plain
            type="danger"
            icon="el-icon-delete"
            circle
            @click="showDeleUserMsgBox(scope.row.account)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--    4.-->

    <el-pagination
      style="margin-top:20px"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pagenum"
      :page-sizes="[2, 4, 6, 8]"
      :page-size="2"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    ></el-pagination>

    <!--     -->
    <!--    -->
  </el-card>
</template>

<script>
export default {
  data () {
    return {
      query: '',
      // 
      userlist: [],
      // 
      total: 1,
      pagenum: 1,
      pagesize: 2
    }
  },
  created () {
    this.getUserList()
  },
  methods: {
    // 
    async getUserList () {
      // API Authorization  token (http.jsaxios)
      // const AUTH_TOKEN = localStorage.getItem('token')
      // this.$http.defaults.headers.common['Authorization'] = AUTH_TOKEN

      const res = await this.$http.get(
        `users?query=${this.query}&pagenum=${this.pagenum}&pagesize=${this.pagesize}`
      )
      console.log(res)
      const {
        meta: { status, msg },
        data
      } = res.data
      if (status == 200) {
        // console.log(data)
        // 1.

        this.userlist = data.data

        this.userlist.forEach((item, index, array) => {
          if (item.juisdiction == 0) {
            array[index].juisdiction = true
          } else {
            array[index].juisdiction = false
          }
          console.log(array[index].juisdiction)
        })
        // this.userstate = this.userlist[0].juisdiction
        // console.log(this.userlist[0].juisdiction)
        console.log(this.userlist)
        // 2.total
        this.total = data.total
        // 3.
         this.$message({
              type: 'success',
              message: msg,
              duration: 1000
            })
      } else {
        // 3.
        this.$message.warning(msg)
      }
    },
    // 
    handleSizeChange (val) {
      // console.log(` ${val} `)
      this.pagesize = val
      this.getUserList()
    },
    handleCurrentChange (val) {
      // console.log(`: ${val}`)
      this.pagenum = val
      this.getUserList()
    },
    // 
    searchUser () {
      // inoutquery 
      this.pagenum = 1
      this.getUserList()
    },
    //  
    loadUserList () {
      this.getUserList()
    },

    //  
    showDeleUserMsgBox (userId) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          const res = await this.$http.delete(`users/${userId}`)

          console.log(res)
          if (res.data.meta.status == 204) {
            this.pagenum = 1
            // 
            this.getUserList()
            // 
            this.$message({
              type: 'success',
              message: res.data.meta.msg,
              duration: 1000
            })
          }
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '',
            duration: 1000
          })
        })
    },

    // 
    async changeMgState (user) {
      const res = await this.$http.put(
        `users/${user.account}/state/${user.juisdiction}`
      )
      console.log(res)
    }
  }
}
</script>

<style scoped>
.box-card {
  height: 100%;
}

.inputSearch {
  width: 300px;
}

.searchRow {
  margin-top: 25px;
}
</style>



